.media_ImageListItem {
    border-radius: 15px;
    background-color: #faf7fa;
    background-image: linear-gradient(#fdeae3, #fdeae3), linear-gradient(#b6c5c0, #b0b0b0), linear-gradient(#f1d6c3, #f1d6c3), linear-gradient(90deg, transparent 20px, #f9e5c7 0, #f9e5c7 calc(100% - 20px), transparent 0);
    background-size: 100% 50px, 50px 10px, 60px 10px, auto 50px;
    background-position: 0 0, 20px 80px, 20px 100px, left 0 bottom 20px;
    background-repeat: no-repeat;
    border: 1px solid #eee;
    box-shadow: 0px 0px 1px #000;
    overflow: hidden;
    cursor: pointer;

}

.media_ImageListItem:after {
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.2);
    z-index: 999;
    left: 0;
    top: 0;
    transition: .1s;
    transform: translate(0, 200px);
}

.media_ImageListItem:hover:after {
    transition: .1s;

    transform: translate(0, 0);

}

.media_ImageListItem img, .media_ImageListItem .MuiImageListItem-img {
    background-color: #fff;
}

